/* General Styles */
body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: #121212;
  color: white;
}

/* Main Container */
.container {
  display: flex;
  min-height: 100vh;
  flex-direction: column; 

}

header{
  margin-top: 0px;
}
/* Left Image Section */
.image-container {
  order: -1; /* Show image first on mobile */
 
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #1e1e1e;
}

/* Profile Image - Mobile */
.profile-image {
width: 100%;
  max-height: 50vh; /* Limit height on mobile */
  object-fit: cover;
  border-top-left-radius: 30%;
  box-shadow: 0px 4px 10px rgba(255, 255, 255, 0.1);
  /* No rounded corners on mobile */
}
@media (min-width: 768px) {
  .container {
    flex-direction: row; /* Side by side on desktop */
    height: 100vh;
  }
  
  .image-container {
    order: 1; /* Move image to right side */
    height: 100vh;
    flex: 1;
  }
  
  .profile-image {
    height: 100vh;
    max-height: none; /* Remove mobile height limit */
    border-top-left-radius: 30%;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
  }
  
  .content {
    order: 0; 
    /* Content on left side */
  }

}

/* Right Content Section */
.content {
  
  flex: 1;
  padding: 20px;
  text-align: center;
}
@media (min-width: 768px) {
  .content {
    padding: 40px;
  }
  .spot{display: flex;
    justify-content: center;
    gap: 50px;
    margin: 40px;
    }
}
