  /* Chatbot button */
.chatbot-btn {
  position: fixed;
  bottom: 20px;
  right: 20px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 50%;
  width: 90px;
  height: 75px;
  font-size: 20px;
  cursor: pointer;
  transition: 0.3s;
}

.chatbot-btn:hover {
  background-color: #0056b3;

}

/* Chatbot container */
.chatbot-box {
  position: fixed;
  bottom: 80px;
  right: 20px;
  width: 300px;
  background: white;
  border-radius: 10px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
  overflow: hidden;
}

/* Chatbot header */
.chat-header {
  background: #007bff;
  color: white;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.chat-header button {
  background: none;
  border: none;
  color: white;
  font-size: 18px;
  cursor: pointer;
}

/* Chat messages */
.chat-body {
  height: 250px;
  overflow-y: auto;
  padding: 10px;
  display: flex;
  flex-direction: column;
}

.user {
  align-self: flex-end;
  background: #007bff;
  color: white;
  padding: 8px 12px;
  border-radius: 15px;
  margin: 5px;
}

.bot {
  align-self: flex-start;
  background: #e9e9e9;
  color: black;
  padding: 8px 12px;
  border-radius: 15px;
  margin: 5px;
}

/* Chat input */
.chat-input {
  display: flex;
  padding: 10px;
  background: white;
  border-top: 1px solid #ddd;
}

.chat-input input {
  flex: 1;
  border: none;
  padding: 8px;
  outline: none;
}

.chat-input button {
  border: none;
  background: #007bff;
  color: white;
  padding: 8px 12px;
  cursor: pointer;
}
.chatbot{margin-top: 0px;}
#strap{margin-top: 40px;}
@media (max-width: 480px) {
  .contact-links {
    display: flex;
    flex-direction: column; /* Stack items vertically */
    align-items: center; 
    /* Center align the links */
  }
  .icon{margin: 5px;}
}
